import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AsistenciaEventosPage } from './asistencia-eventos.page';

const routes: Routes = [
  {
    path: '',
    component: AsistenciaEventosPage
  },
  {
    path: 'qr',
    loadChildren: () => import('../asistencia-eventos-qr/asistencia-eventos-qr.module').then(m => m.AsistenciaEventosQrPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AsistenciaEventosPageRoutingModule { }
