import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { alertCommunicationError } from 'src/app/commons/user-feedback';
import { Trabajador } from 'src/app/entities/trabajador';
import { ContextService } from 'src/app/services/context.service';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
  selector: 'app-asistencia-eventos',
  templateUrl: './asistencia-eventos.page.html',
  styleUrls: ['./asistencia-eventos.page.scss'],
})
export class AsistenciaEventosPage implements OnInit {

  opcion: string;

  user: Trabajador;

  constructor(
    private context: ContextService,
    private alertCtrl: AlertController,
    private router: Router,
    private utils: UtilsService
  ) { }

  ngOnInit(): void {
    this.setUser();
  }

  ionViewWillEnter(): void {
    this.opcion = 'asistenciaEventos';
  }

  ionViewWillLeave(): void {
    this.opcion = undefined;
  }

  private async setUser(): Promise<void> {
    await this.utils.presentLoading();
    const context = await this.context.get();
    this.utils.dismissLoading();

    if (!context) {
      alertCommunicationError(this.alertCtrl);
      this.router.navigate(['/home']);

      return;
    }

    this.user = context.trabajador;
  }

}
